import React, {Component} from 'react';
import {reduxForm, Field} from 'redux-form';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {toastr} from 'react-redux-toastr';
import {changeTab} from '../../actions/tab';
import {MESSAGES, TABS} from '../../utils/strings';
import { required, renderField, validate } from './utils';
import { addCustomUrl } from '../../actions/privateUrls';
import { getPlan } from '../../actions/plan'
import PropTypes from 'prop-types';
import './form.scss';

class PuPForm extends Component {
    constructor(props) {
        super(props);
        this.onSubmit = this.onSubmit.bind(this);
        this.state = {
            loadingData: false,
        };
        this.wpData = {
            isLicenseExpired: window.pda_gold_v3_data.is_license_expired,
        }
    }

    render() {
        const { handleSubmit, pristine, submitting, plan } = this.props;
        const isPlanLite = plan.planName === 'lite';
        const form =  (
            <div className="pup-form">
            <form className="pda_wrap_form_custom_private_link" onSubmit={handleSubmit(this.onSubmit)}>
                <div>
                    <p>
                        {/*<p>{MESSAGES.FILE_TO_CONFIG} <b>"{post.title}"</b> file</p>*/}
                    </p>
                </div>
                <div>
                    <p>
                        <Field name="is_prevented" id="is_prevented" component="input" type="checkbox"/> <label htmlFor="is_prevented">Is this private URL active?</label>
                    </p>
                </div>
                <Field name="url" type="text"
                       component={renderField} label="Custom private URL slug (no space)"
                       validate={[ required ]} />
                <Field name="limit_downloads" type="number"
                       component={renderField} label="Download limit (leave blank for unlimited re-downloads)" />
                <Field name="expired_days" type="number" component={renderField} label="Download expiry (enter the number of days before this download link expires, or leave blank)"/>
                <div className="pup-actions">
                    <button className="btn button action" type="submit" disabled={isPlanLite || pristine || submitting || this.wpData.isLicenseExpired}>Submit</button>
                </div>
            </form>
            </div>
        );

        return this.state.loadingData ? this.renderLoading() : form;
    }


    renderLoading() {
        return (
            <div className="loading">Loading Data...</div>
        )
    }

    onSubmit(values) {
        const { postId, addCustomUrl } = this.props;
        this.setState(...this.state, {
            loadingData: true
        });
        addCustomUrl(postId, values)
            .then(() => {
                const { customUrlResponse, handleChangeTab} = this.props;
                this.setState(...this.state, {
                    loadingData: false
                });
                if(customUrlResponse.isError) {
                    toastr.error("Opps! Unable to create new custom private link due to duplicate slug.");
                } else {
                    toastr.success(MESSAGES.CREATE_NEW_PRIVATE_LINK);
                    changeTab(TABS.TABLE);
                    handleChangeTab(TABS.TABLE);
                }
            });
    }
}

PuPForm.propTypes = {
    postId : PropTypes.string.isRequired,
    plan: PropTypes.object
}

const Form = reduxForm({
    form: 'PupForm',  // a unique identifier for this form
    validate,
})(PuPForm);

const mapActionsToProps = (dispatch) => {
    return bindActionCreators({addCustomUrl, changeTab, getPlan}, dispatch);
}

const mapStateToProps = (state) => {
    return {
        customUrlResponse : state.createUrls,
        initialValues: {
            is_prevented: true
        },
        plan: state.getPlan
    }
}

export default connect(mapStateToProps, mapActionsToProps)(Form);



