import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { connect } from 'react-redux';
import { Popup, WPReTable, Tab, ColumnConfig } from 'wp-react-toolkits';
import Immutable from "immutable";
import {toastr} from 'react-redux-toastr';
import ReduxToastr from 'react-redux-toastr'; ///https://github.com/diegoddox/react-redux-toastr
import store from './store';
import { bindActionCreators } from 'redux';
import { fetchUrls, newUrl, deleteUrl, updateUrlStatus, updateDataUrl, protectThisFile, unProtectThisFile, isItProtectedFile, setDefault } from './actions/privateUrls';
import { syncFileToS3, unSyncFileFromS3, checkStatusFile } from './actions/s3';
import { getTotalPrivateLinks } from './actions/stats';
import { changeTab } from './actions/tab';
import { getPlan } from './actions/plan';
import { POST_TYPES } from './configs';
import { BUTTON_LABELS, MESSAGES, TABS } from './utils/strings';
import UI_Utils from './utils/ui';
import PupForm from './components/form';
import IpBlockForm from './components/ipblock-form';
import UserAccessForm from './components/useraccess-form';
import './index.css';
import 'font-awesome/scss/font-awesome.scss';
import 'wp-react-toolkits/style/table.css';
import 'react-redux-toastr/src/styles/index.scss';

import { getUserRolesIpBlock} from './actions/ipBlock';

import AffiliateProgram from './components/affiliate-program';
import PrivateLinkUserForm from './components/private-link-user';
import planConfig from './configs/plan';

class App extends React.Component {
    constructor(props) {
        super(props);
        this.handleClosePopup = this.handleClosePopup.bind(this);
        this.handleShowPopup = this.handleShowPopup.bind(this);
        this.handleProtectFileEvent = this.handleProtectFileEvent.bind(this);
        this.handleAddNewPrivateLink = this.handleAddNewPrivateLink.bind(this);
        this.handleEditPrivateUrl = this.handleEditPrivateUrl.bind(this);
        this.handleCopyPrivateLink = this.handleCopyPrivateLink.bind(this);
        this.refreshData = this.refreshData.bind(this);
        this.handleChangeTab = this.handleChangeTab.bind(this);
        this.changeToCustomForm = this.changeToCustomForm.bind(this);
        this.handleProtectThisFile = this.handleProtectThisFile.bind(this);
        this.handleUnProtectThisFile = this.handleUnProtectThisFile.bind(this);
        this.setDefaultPrivateLink = this.setDefaultPrivateLink.bind(this);
        this.checkIsProtectedFile = this.checkIsProtectedFile.bind(this);
        this.syncFileToS3 = this.syncFileToS3.bind(this);
        this.unSyncFile = this.unSyncFile.bind(this);
        this.reload = this.reload.bind(this);
        this.changeToFileAccessPermission = this.changeToFileAccessPermission.bind(this);
        this.handleProtectFileEvent();
        this.state = {
            currentPostId: null,
            loadingData: false,
            editingId: null,
            isItProtected: true,
            post: {
                title: '',
                edit_url: '',
                is_file_deleted: false,
            }
        }
        this.wpData = {
          isLicenseExpired: window.pda_gold_v3_data.is_license_expired
        }
    }

    render() {
        return (
            <div>
                <ReduxToastr
                    timeOut={2000}
                    newestOnTop={false}
                    preventDuplicates={true}
                    position="bottom-right"
                    transitionIn="fadeIn"
                    transitionOut="fadeOut"
                />
                <Popup
                    label='Pop up label'
                    closeTitle='Close popup'
                    insideElement={this.renderTabs()}
                    closeBtn={this.renderCloseBtn()}
                    onRef={ref => {this.child = ref}}
                />
            </div>
        )
    }

    renderTabs() {
        const { currentTab, plan } = this.props;
        const { post } = this.state;
        let tabs = [{
            name: 'Prevent Direct Access',
            panel: this.renderTableTab()
        }];

        if( this.state.isItProtected ) {
            tabs.push({
                name: 'New Private Link',
                panel: this.renderFormTab()
            })
            if ( typeof(ip_block_server_data) !== 'undefined' && ip_block_server_data.pda_magic_link_activated == -1) {
                tabs.push({
                    name: 'Shared Private Link',
                    panel: this.renderTabPrivateLinkUser()
                })
            }
            tabs.push({
                name: 'File Access Permission',
                panel: this.renderUserAccess()
            });
            if( typeof(ip_block_server_data) !== 'undefined' && ip_block_server_data.ip_block_activated == -1){
                tabs.push({
                    name: 'IP Restriction',
                    panel: this.renderIpBlock()
                })
            }
            if (plan.planName !== 'lite') {
                tabs.push({
                    name: 'Invite & Earn',
                    panel: this.renderAffiliateProgram()
                });
            }
        }

        return  (<div className="fix-css-conflict-yoast-seo">
                    <Tab tabs={tabs} onRef={ref => {this.childTab = ref}} defaultTab={currentTab.tab}/>
                </div>);
    }

    handleChangeTab(index) {
        this.childTab.handleSelect(index);
        this.props.fetchUrls(this.state.currentPostId, this.state.type);
        this.reload();
    }

    handleChangeExpiredDate = (val, id) => {
        const {updateDataUrl} = this.props;
        const current = new Date();
        const datetime = `${val} ${current.getHours()}:${current.getMinutes()}:${current.getSeconds()}`;
        const timeStamp = Date.parse(datetime) / 1000;
        updateDataUrl(id, {expired_date: timeStamp})
            .then(() => {
                const {updateUrlResponse, fetchUrls} = this.props;
                if (updateUrlResponse.result.isError) {
                    toastr.error("Opps! Unable to update the link data. Please try again or contact plugin owner.");
                } else {
                    const { currentPostId, type } = this.state;
                    toastr.success(MESSAGES.UPDATE_LINK_DATA);
                    fetchUrls(currentPostId, type).then(() => {
                        this.setState(...this.state, {
                            editingId: null
                        });
                    });
                }
            })
    }

    handleChangeLimitDownloads(val, id) {
        const {updateDataUrl} = this.props;
        if(val <= 0) {
            val = null;
        }
        updateDataUrl(id, {limit_downloads: val})
            .then(() => {
                const {updateUrlResponse, fetchUrls} = this.props;
                if (updateUrlResponse.result.isError) {
                    toastr.error("Opps! Unable to update the link data. Please try again or contact plugin owner.");
                } else {
                    const { currentPostId, type } = this.state;
                    toastr.success(MESSAGES.UPDATE_LINK_DATA);
                    fetchUrls(currentPostId, type).then(() => {
                        this.setState(...this.state, {
                            editingId: null
                        });
                    });
                }
            });
    }

    renderTableTab() {
        const { privateUrls, plan } = this.props;
        const { loadingData } = this.state;
        if ( privateUrls.all && privateUrls.all.length > 0 )
        console.log('Debug', typeof  privateUrls.all[0].limit_downloads);
        const data = Immutable.List(privateUrls.all);
        const time_now = new Date();
        const unit_time_now = Math.ceil(time_now/1000);
        const configs = {
            columnsConfig: [
                new ColumnConfig('Private Download Links', 'full_url', false, 600, ({cellData, rowData}) => {
                    const limit = rowData.limit_downloads == null || Number.parseInt(rowData.limit_downloads) > Number.parseInt(rowData.hits_count) ? true : false;
                    const isDefault = rowData.is_default === "1"
                        && rowData.is_prevented === "1"
                        && limit
                        && (rowData.expired_date === null ||  rowData.expired_date > unit_time_now);
                    return isDefault ? <div className={( pda_gold_v3_data.magic_link_activated == -1 ) ? 'is_default' : ''}><a target="_blank" href={cellData}>{cellData}</a></div> : <a target="_blank" href={cellData}>{cellData}</a>;
                }),
                new ColumnConfig('Is it active?', 'is_prevented', false, 150, ({cellData, rowData}) => {
                    const { ID, expired_date, limit_downloads, hits_count } = rowData;
                    const limit = limit_downloads == null || Number.parseInt(limit_downloads) > Number.parseInt(hits_count) ? true : false;
                    const validity = expired_date == null || Number.parseInt(expired_date) > unit_time_now ? true : false;
                    console.log('Test expired_date', typeof expired_date);
                    console.log(expired_date);
                    return cellData == 1 && validity && limit ? <i className='fa fa-check fa-lg pda-active-link '/>
                        : <i className='fa fa-times fa-lg pda-inactive-link'/>
                }),
                new ColumnConfig('Total Clicks', 'hits_count', true, 150),

                new ColumnConfig('Download Limit', 'limit_downloads', true, 250, ({cellData, rowData}) => {
                    return this.state.editingId === rowData.ID ?
                        (
                            <div>
                                <input type="number" min={1} defaultValue={cellData}
                                       onBlur={(evt) => this.handleChangeLimitDownloads(evt.target.value, rowData.ID)}/>
                            </div>
                        )
                        :
                        (
                            <div>
                                {cellData ? `${cellData} ${UI_Utils.renderAttempt(cellData)}` : "Unlimited"}
                            </div>
                        )
                }, ({
                        columnData,
                        dataKey,
                        disableSort,
                        label,
                        sortBy,
                        sortDirection
                    }) => {
                    const showSortIndicator = sortBy === dataKey;
                    const children = [
                        <span
                            className="ReactVirtualized__Table__headerTruncatedText pda-tooltip"
                            key="label"
                            title={label}
                        >
                    {label} <i className="fa fa-lightbulb-o " aria-hidden="true">
                    </i>
                    <span className="tooltiptext">Set a download limit for this URL. Default: Unlimited.</span>
                </span>
                    ];

                    if (showSortIndicator) {
                        children.push(
                            <SortIndicator key="SortIndicator" sortDirection={sortDirection}/>
                        );
                    }

                    return children;

                }),

                new ColumnConfig('Download Expiry', 'expired_date', false, 300,({cellData, rowData}) => {

                    if(this.state.editingId === rowData.ID) {
                        const today = new Date();
                        today.setDate(today.getDate() + 1);
                        const min = UI_Utils.changeDateFormat(today);

                        const currentDay = new Date();
                        currentDay.setDate(currentDay.getDate());
                        const currVal = UI_Utils.changeDateFormat(currentDay);

                        return <div><input type="date" min={min} defaultValue={currVal} onBlur={(evt) => this.handleChangeExpiredDate(evt.target.value, rowData.ID)}/></div>;
                    } else {
                        if(cellData) {
                            var date = new Date(cellData * 1000);
                            // cellData = date.toLocaleDateString();
                            cellData = date.toLocaleString();
                        } else {
                            cellData = 'Never';
                        }
                        return <div>{cellData}</div>;
                    }

                },({

                       columnData,
                       dataKey,
                       disableSort,
                       label,
                       sortBy,
                       sortDirection

                   }) => {

                    const showSortIndicator = sortBy === dataKey;
                    const children = [
                        <span
                            className="ReactVirtualized__Table__headerTruncatedText pda-tooltip"
                            key="label"
                            title={label}
                        >
                    {label} <i className="fa fa-lightbulb-o " aria-hidden="true">
                    </i>
                    <span className="tooltiptext">Set an expiration date for this URL. Default: Never.</span>
                </span>
                    ];

                    if (showSortIndicator) {
                        children.push(
                            <SortIndicator key="SortIndicator" sortDirection={sortDirection}/>
                        );
                    }

                    return children;

                }),

                new ColumnConfig('Created Time', 'time', true, 300, ({cellData, rowData}) => {
                    return new Date(cellData * 1000).toLocaleString()
                }),

                new ColumnConfig('', 'ID', true, 250, ({cellData, rowData}) => {
                    const {ID, expired_date, limit_downloads, hits_count, is_prevented } = rowData;

                    const isUnderLimit = limit_downloads == null || Number.parseInt(limit_downloads) > Number.parseInt(hits_count) ? true : false;
                    const isStillValidDate = expired_date == null || Number.parseInt(expired_date) > unit_time_now ? true : false;

                    const changeStatusLabel = is_prevented === "1" || isUnderLimit || isStillValidDate ? 'De-activate' : 'Activate';
                    const activateMagicLink = ( pda_gold_v3_data.magic_link_activated == -1 ) ? '' : 'hidden';

                    const ischangeStatusLabel = is_prevented !== "1" || ! ( isUnderLimit && isStillValidDate ) ? "pdav3_table_actions hidden" : "pdav3_table_actions";
                    const isPlanLite = plan.planName === 'lite';
                    return (
                        <div>
                        <span title="Edit"
                              onClick={isPlanLite ? '' : this.handleEditPrivateUrl.bind(this, ID, cellData) }
                              className="pdav3_table_actions">
                              <i className='fa fa-edit fa-lg'/>
                        </span>
                            <span title="Copy to clipboard"
                                  onClick={this.handleCopyPrivateLink.bind(this, cellData)}
                                  className="pdav3_table_actions">
                              <i className="fa fa-copy fa-lg"/>
                            </span>
                            <span title="Set default private link" onClick={this.setDefaultPrivateLink.bind(this, ID)}
                                 className={ ischangeStatusLabel + " " + activateMagicLink }><i className="fa fa-cog fa-lg"/></span>
                            <span title={changeStatusLabel} onClick={isPlanLite ? '' : this.handleSetLinkStatus.bind(this, ID, rowData.is_prevented)}
                                  className="pdav3_table_actions"><i className="fa fa-chain-broken fa-lg"></i>
                            </span>
                            <span title="Delete link"
                                  onClick={isPlanLite ? '' : this.handleDeletePrivateLink.bind(this, cellData)}
                                  className="pdav3_table_actions">
                              <i className="fa fa-remove fa-lg"/>
                        </span>
                        </div>
                    );
                })
            ]
        };
        const wp_table = this.state.isItProtected ? <WPReTable list={data} config={configs}/> : null;
        const table = (
            <div>
                <div className='pda-toolbar'>
                    {this.renderTableToolbar()}
                </div>
                <hr className= {(this.state.isItProtected) ? "" : "hidden" }/>
                {wp_table}
            </div>
        )
        const tableData = (
            loadingData ? UI_Utils.renderLoadingData() : table
        );
        return tableData;

    }

    renderAffiliateProgram() {
        return <AffiliateProgram/>;
    }

    renderFormTab() {
        return this.state.loadingData ? UI_Utils.renderLoadingData() : <PupForm type={this.state.type} postId={this.state.currentPostId} handleChangeTab={this.handleChangeTab}/>;
    }

    renderTabPrivateLinkUser() {
        return <PrivateLinkUserForm postId={this.state.currentPostId} handleChangeTab={this.handleChangeTab}/>;
    }

    renderIpBlock() {
        return <IpBlockForm postId={this.state.currentPostId}/>;
    }
    renderUserAccess() {
        return this.state.loadingData ? UI_Utils.renderLoadingData() : <UserAccessForm postId={this.state.currentPostId} handleChangeTab={this.handleChangeTab}/>;
    }

    renderTableToolbar() {
        const { post } = this.state;
        const { RolesAccess, fileStatus, fileS3Status, plan } = this.props;
        if( typeof(fileStatus.role_setting) !== "undefined" && fileStatus.role_setting !== null) {
            var roles_access = "";
            switch (fileStatus.role_setting.file_access_permission) {
                case "admin_users":
                    roles_access = "Admin users";
                    break;
                case "author":
                    roles_access = "The file's author";
                    break;
                case "logged_users":
                    roles_access = "Logged-in users";
                    break;
                case "blank":
                    roles_access = "No one";
                    break;
                case "anyone":
                    roles_access = "Anyone";
                    break;
                case "custom_roles":
                    roles_access = fileStatus.role_setting.whitelist_roles;
                    break;
                default:
                    roles_access = "No one";
                    break;
            }
        }

        if(typeof(RolesAccess.userRolesAccess) != "undefined") {
            var access_roles = "";
            switch (RolesAccess.userRolesAccess.type) {
                case "admin-user":
                    access_roles = "Admin users";
                    break;
                case "author":
                    access_roles = "The file's author";
                    break;
                case "memberships":
                    access_roles = pda_gold_v3_data.pda_membership_integration_activated == -1 ? "Custom memberships" : roles_access;
                    break;
                case "logger-in-user":
                    access_roles = "Logged-in users";
                    break;
                case "blank":
                    access_roles = "No one";
                    break;
                case "anyone":
                    access_roles = "Anyone";
                    break;
                case "custom-roles":
                    access_roles = RolesAccess.userRolesAccess.roles.replace(/;/g," - ");
                    break;
                case "default":
                    access_roles = roles_access;
                    break;
                default:
                    access_roles = roles_access;
                    break;
            }
        }

        const s3Btn = post.s3_link
            ? (<input type="submit" className="button btn action" value={BUTTON_LABELS.UN_SYNC_TO_S3}
                    onClick={this.unSyncFile} />)
            : (<input type="submit" className="button btn action" value={BUTTON_LABELS.SYNC_TO_S3}
                      onClick={this.syncFileToS3} />);
        const isS3Activated = pda_gold_v3_data.pda_s3_activated;
        const isPlanLite = plan.planName === 'lite'; // if plan is lite version
        const privateLinksBtn = (
            <span>
                <input disabled={this.wpData.isLicenseExpired || isPlanLite} type="submit" className="button btn action" value={BUTTON_LABELS.GENERATE_LINK}
                       onClick={this.handleAddNewPrivateLink} />
                <input disabled={this.wpData.isLicenseExpired} type="submit" className="button btn action" value={BUTTON_LABELS.ADD_CUSTOM_LINK}
                       onClick={this.changeToCustomForm} />
            </span>
        );

        const protectBtnLabel = this.state.isItProtected
            ? <button className="button btn action unprotect-btn" onClick={this.handleUnProtectThisFile}>{BUTTON_LABELS.UN_PROTECT_THIS_FILE}</button>
            : <button type="submit" className="button btn action protect-btn" onClick={this.handleProtectThisFile}>{BUTTON_LABELS.PROTECT_FILE}</button>;
        const unprotect = (<span className="protection-status pda-unprotected"> <i className="fa fa-times-circle" aria-hidden="true"></i> unprotected</span>);

        const protect = (<span className="protection-status"><i className="fa fa-check-circle" aria-hidden="true"></i> protected</span>);

        const title = "This file is accessible to " + access_roles + ". Click to edit.";
        const access = this.state.isItProtected ? (<span title={title} onClick={this.changeToFileAccessPermission} className="access_permision protection-status"><i className="fa fa-user-circle-o" aria-hidden="true"></i> {access_roles}</span>) : "";
        const showAccessRoles = post.is_file_deleted ? '' : access;

        const s3_link = post.s3_link ? ( fileS3Status.status ? (<span className="protection-status offloaded-to-s3"><i className="fa fa-check-circle" aria-hidden="true"></i> offloaded to S3</span>) : (<span className="protection-status synced-to-s3"><i className="fa fa-check-circle" aria-hidden="true"></i> synced to S3</span>)) : (<span></span>);

        return (
            <div>
                <div>
                    <p>{MESSAGES.FILE_TO_CONFIG} <a className="pda-orginal-link" target="_blank" href={post.edit_url}>{ this.parseHtmlEnteties(post.title) }</a> file. { (this.state.isItProtected) ? protect : unprotect} {showAccessRoles} {s3_link} </p>
                </div>
                <div className="pup-form actions">
                    <div>
                        { protectBtnLabel }
                        { this.state.isItProtected ? privateLinksBtn : null}
                        { isS3Activated === "1" && s3Btn }
                        <span onClick={this.refreshData} className= {(this.state.isItProtected) ? "" : "hidden" }><i title="Refresh Data" className="fa fa-refresh fa-lg pup-reload " /></span>
                        {this.renderTableSummary()}
                    </div>
                </div>
            </div>
        )
    }

    parseHtmlEnteties(str) {
        return str.replace(/&#([0-9]{1,4});/gi, function(match, numStr) {
            var num = parseInt(numStr, 10); // read num as normal number
            return String.fromCharCode(num);
        });
    }

    renderTableSummary() {
        const { total } = this.props;
        if(pda_gold_v3_data.stats_activated == -1) {
            return (
                <div className={(this.state.isItProtected) ? 'pda-tbl-summary' : 'hidden'}>
                    <h4 className="pda-sum">Total Private Links: {total.total_private_link} </h4>
                    <h4 className="pda-sum">Total Clicks: {total.total_click} </h4>
                </div>
            );
        }
    }

    renderCloseBtn() {
        return (<span title="Close"
                      className="close_btn"
                      onClick={this.handleClosePopup}><i className="fa fa-times fa-lg"/></span>);
    }

    handleShowPopup() {
        this.child.handleOpenPopup();
    }

    handleClosePopup() {
        const { changeTab } = this.props;
        changeTab(TABS.TABLE);
        this.child.handleClosePopup();
        if(this.state.isItProtected) {
            jQuery(`#pda-v3-text_${this.state.currentPostId}`).html('<i class="fa fa-check-circle" aria-hidden="true"></i> protected');
            jQuery(`#pda-v3-text_${this.state.currentPostId}`).removeClass('pda-unprotected');
            jQuery(`#pda-v3-text_${this.state.currentPostId}`).prop('title', 'This file is protected');
        } else {
            jQuery(`#pda-v3-text_${this.state.currentPostId}`).html('<i class="fa fa-times-circle" aria-hidden="true"></i> unprotected');
            jQuery(`#pda-v3-text_${this.state.currentPostId}`).addClass('pda-unprotected');
            jQuery(`#pda-v3-text_${this.state.currentPostId}`).prop('title', 'This file is unprotected');
        }
    }

    handleProtectFileEvent() {
        const self = this;
        jQuery('.pda_gold_btn').on('click', function (evt) {
            self.handleShowPopup();
            const currentPostId = jQuery(this).attr('id').split('-')[1];
            console.log("Current post id", currentPostId)
            self.setState(...self.state, {
                currentPostId,
                loadingData: true
            });
            self.checkIsProtectedFile(currentPostId);
        });
    }

    handleAddNewPrivateLink(evt) {
        evt.preventDefault();
        const { newUrl } = this.props;
        const { currentPostId } = this.state;
        this.setState(...this.state, {
            loadingData: true
        });
        if(currentPostId) {
            newUrl(currentPostId)
                .then(() => {
                    this.setState(...this.state, {
                        loadingData: false
                    });
                    const { createdUrl, fetchUrls  } = this.props;
                    if(createdUrl.isError) {
                        toastr.error(MESSAGES.ERR_CREATE_NEW_PRIVATE_LINK);
                    } else {
                        toastr.success(MESSAGES.CREATE_NEW_PRIVATE_LINK);
                        fetchUrls(currentPostId);
                    }
                });
        }
    }

    handleProtectThisFile(evt) {
        evt.preventDefault();
        const { protectThisFile, plan } = this.props;
        const { currentPostId } = this.state;
        this.setState(...this.state, {
            loadingData: true
        });
        protectThisFile(currentPostId)
            .then(() => {
                this.setState(...this.state, {
                    loadingData: false
                });
                const { protectFile } = this.props;
                if(protectFile.error) {
                    toastr.error(protectFile.response);
                } else {
                    if (plan.planName === 'lite' && protectFile.response.error) {
                        toastr.error( protectFile.response.error );
                    }
                    else {
                        toastr.success(MESSAGES.PROTECT_FILE);
                        this.reload();
                    }
                }
            });
    }

    handleEditPrivateUrl(editingId) {
        this.setState(...this.state, {editingId})
    }

    handleSetLinkStatus(id, is_prevented) {
        const { updateUrlStatus} = this.props;
        is_prevented = is_prevented === "1" ? true : false;
        this.setState(...this.state, {
            loadingData: true
        });
        updateUrlStatus(id, is_prevented)
            .then(() => {
               const { updateUrlResponse, fetchUrls } = this.props;
               if(updateUrlResponse.result.isError) {
                   toastr.error(MESSAGES.ERR_UPDATE_PRIVATE_LINK);
               } else {
                   const { currentPostId, type } = this.state;
                   toastr.success(MESSAGES.UPDATE_LINK_DATA);
                   fetchUrls(currentPostId, type);
               }
                this.setState(...this.state, {
                    loadingData: false
                });
            });
    }

    handleCopyPrivateLink(ID) {
        const { privateUrls } = this.props;
        const link = privateUrls.all.find(f => f.ID == ID);
        UI_Utils.handleCopyBtn(link.full_url);
    }

    checkIsProtectedFile(currentPostId) {
        const { isItProtectedFile, fetchUrls, checkStatusFile } = this.props;
        checkStatusFile(currentPostId);
        isItProtectedFile(currentPostId)
            .then(() => {
                const { fileStatus, getTotalPrivateLinks, getUserRolesIpBlock } = this.props;

                if(fileStatus.error) {
                    toastr.error(MESSAGES.ERR_CALL_DATA);
                    this.setState(...this.state, {
                        loadingData: false,
                    });
                } else {
                    this.setState(...this.state, {
                        isItProtected: fileStatus.is_protected,
                        post: fileStatus.post
                    });
                    if( fileStatus.is_protected ) {
                        fetchUrls(currentPostId)
                            .then(() => {
                                this.setState(...this.state, {
                                    loadingData: false,
                                });
                            });
                        if (pda_gold_v3_data.stats_activated == -1) {
                            getTotalPrivateLinks(currentPostId);
                        }
                        getUserRolesIpBlock(currentPostId);
                    } else {
                        this.setState(...this.state, {
                            loadingData: false,
                        });
                    }
                }
            });
    }

    reload() {
        const { currentPostId } = this.state;
        this.setState(...this.state, {
            loadingData: true
        });
       this.checkIsProtectedFile(currentPostId);
    }

    refreshData(evt) {
        evt.preventDefault();
        this.reload();
    }

    handleDeletePrivateLink(id) {
        const toastrConfirmOptions = {
            onOk: () => {
                const { deleteUrl } = this.props;
                deleteUrl(id).then(() => {
                    const {deleteUrlResponse, fetchUrls} = this.props;
                    if (deleteUrlResponse.result.isError) {
                        toastr.error(MESSAGES.ERR_DELETE_PRIVATE_LINK);
                    } else {
                        toastr.success(MESSAGES.DELETE_PRIVATE_LINK);
                        fetchUrls(this.state.currentPostId);
                    }
                });
            },
            okText: 'Yes',
            cancelText: 'No'
        };
        toastr.confirm(MESSAGES.CONFIRM_DELETE_LINK, toastrConfirmOptions);
    }

    changeToCustomForm(evt) {
        evt.preventDefault();
        const { changeTab } = this.props;
        changeTab(TABS.FORM);
        this.childTab && this.childTab.handleSelect(TABS.FORM);
    }

    changeToFileAccessPermission(evt) {
        evt.preventDefault();
        const { changeTab } = this.props;
        let tabFileAccessPermission = TABS.FILE_ACCESS_PERMISSION;
        if (ip_block_server_data.pda_magic_link_activated == -1) {
            tabFileAccessPermission += 1;
        }
        changeTab(tabFileAccessPermission);
        this.childTab && this.childTab.handleSelect(tabFileAccessPermission);
    }

    handleUnProtectThisFile(evt) {
        evt.preventDefault();
        const { unProtectThisFile } = this.props;
        const { currentPostId } = this.state;
        this.setState(...this.state, {
            loadingData: true
        });
        unProtectThisFile(currentPostId)
            .then(() => {
                this.setState(...this.state, {
                    loadingData: false
                });
                const { protectFile } = this.props;
                if(protectFile.error) {
                    toastr.error(protectFile.response);
                } else {
                    toastr.success(MESSAGES.UN_PROTECT_FILE);
                   this.reload();
                }
            });
    }

    setDefaultPrivateLink( id ) {
        const { setDefault } = this.props;
        this.setState(...this.state, {
            loadingData: true
        });
        setDefault(id, this.state.currentPostId)
            .then(() => {
                this.reload();
                this.setState(...this.state, {
                    loadingData: false
                });
            });
    }

    syncFileToS3() {
        const { currentPostId } = this.state;
        const { syncFileToS3 } = this.props;
        this.setState(...this.state, {
            loadingData: true
        });
        syncFileToS3( currentPostId )
            .then(() => {
                this.setState(...this.state, {
                    loadingData: false
                });
                const { s3Response } = this.props;
                if ( s3Response.isError ) {
                    toastr.error(s3Response.message);
                } else {
                    this.reload();
                }
                jQuery(`#pda-s3-sync-file_${currentPostId}`).text('Unsync');
                const { checkStatusFile } = this.props;
                checkStatusFile(currentPostId).then(() => {
                    const { fileS3Status } = this.props;
                    if ( fileS3Status.status ) {
                        jQuery(`#pda-v3-wrap-status_${currentPostId}`).append(
                            `<span id="pda-s3-sync-file-status_${currentPostId}" title="This file is offloaded to Amazon S3" class="protection-status offloaded-to-s3"><i class="fa fa-check-circle" aria-hidden="true"></i> offloaded</span>`
                        );
                    } else {
                        jQuery(`#pda-v3-wrap-status_${currentPostId}`).append(
                            `<span id="pda-s3-sync-file-status_${currentPostId}" title="This file is synced to Amazon S3" class="protection-status synced-to-s3"><i class="fa fa-check-circle" aria-hidden="true"></i> synced</span>`
                        );
                    }
                });
            });

    }

    unSyncFile() {
        const { currentPostId } = this.state;
        const { unSyncFileFromS3 } = this.props;
        this.setState(...this.state, {
            loadingData: true
        });
        unSyncFileFromS3( currentPostId )
            .then(() => {
                this.setState(...this.state, {
                    loadingData: false
                });
                const { s3Response } = this.props;
                console.log('Yolo', s3Response);
                if ( s3Response.isError ) {
                    toastr.error(s3Response.message);
                } else {
                    this.reload();
                }
                jQuery(`#pda-s3-sync-file_${currentPostId}`).text('Sync');
                jQuery(`#pda-s3-sync-file-status_${currentPostId}`).remove();
            });
    }
}

const mapStateToProps = state => {
    return {
        privateUrls: state.privateUrls,
        createdUrl: state.createUrls,
        deleteUrlResponse: state.deleteUrl,
        updateUrlResponse: state.updateUrl,
        currentTab: state.tab,
        protectFile: state.protectFile,
        fileStatus: state.isProtectedFile,
        total: state.total,
        RolesAccess: state.userRolesAccess,
        fileS3Status: state.fileS3Status,
        s3Response: state.s3Link,
        plan: state.getPlan
        // allRolesSetting: state.allRolesSetting
    }
}

store.dispatch(getPlan());


const mapDispatchToProps = dispatch => {
        return bindActionCreators({fetchUrls, newUrl, deleteUrl, updateUrlStatus,
            changeTab, updateDataUrl, protectThisFile, unProtectThisFile,
            isItProtectedFile, setDefault, getTotalPrivateLinks, syncFileToS3,
            getPlan, unSyncFileFromS3, getUserRolesIpBlock, checkStatusFile }, dispatch);
};




const PPPApp = connect(mapStateToProps, mapDispatchToProps)(App);

document.addEventListener('DOMContentLoaded', function () {
    var divElement = document.createElement("div");
    divElement.setAttribute("class", "pda_v3_react_root");
    document.body.appendChild(divElement);
    ReactDOM.render(
        <Provider store={store}>
            <PPPApp />
        </Provider>,
        divElement);
});




