import React, {Component} from 'react';

import {reduxForm, Field} from 'redux-form';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {toastr} from "react-redux-toastr";
import {addUserRoles, getUserRolesIpBlock, getMembersSelect} from '../../actions/ipBlock';
import {getAllUserRoles} from '../../actions/ipBlock';
import { getPlan } from '../../actions/plan'

import {MESSAGES, TABS} from "../../utils/strings";

import Select from 'react-select';
import 'react-select/dist/react-select.css';
import './useraccess-form.scss';
import {membersSelect} from "../../reducers/reducer_user_roles";
import {changeTab} from "../../actions/tab";

class UserAccessForm extends Component {

    constructor(props) {
        super(props);
        this.massageMembershipSelect = this.massageMembershipSelect.bind(this);
        this.massageWooSubSelect = this.massageWooSubSelect.bind(this);
        this.optionMembership = this.optionMembership.bind(this);
        this.optionWooSubMembership = this.optionWooSubMembership.bind(this);
        this.renderMembershipTable = this.renderMembershipTable.bind(this);
        this.setValueForMemberships = this.setValueForMemberships.bind(this);

        this.state = {
            value: [],
            loading: false,
            selectedOption: [],
            value_user_access_manager: [],
            value_memberships_2: [],
            value_paid_memberships_pro: [],
            value_woo_memberships: [],
            value_woo_subscriptions: [],
        };
    }

    renderMembershipTable(pda_gold_v3_data, values) {
        const keys = ["user_access_manager", "memberships_2", "paid_memberships_pro", "woo_memberships", "woo_subscriptions"];
        const memberships_tmp = keys.map(k => {
            const member = k.split("_");
            const result = member.map( mem => mem.charAt(0).toUpperCase() + mem.slice(1));
            const label = result.join(" ") + ":";
            const funcchange = 'handleChange' + result.join("");
            if(k === "woo_subscriptions") {
                return {
                    id: k,
                    label: label,
                    onChangeFunc: this[funcchange],
                    value: values["value_" + k],
                    options: this.optionWooSubMembership(k)
                }
            }
            return {
                id: k,
                label: label,
                onChangeFunc: this[funcchange],
                value: values["value_" + k],
                options: this.optionMembership(k)
            }
        });

        const membershipRows = memberships_tmp
            .filter(k => pda_gold_v3_data[k.id])
            .map(data => {
                return (
                    <tr key={data.id}>
                        <td>{data.label}</td>
                        <td>
                            <Select
                                className={`${data.id} memberships`}
                                name={data.id}
                                multi
                                value={data.value}
                                onChange={data.onChangeFunc}
                                placeholder="Select..."
                                options={data.options}
                            />
                        </td>
                    </tr>
                );
            });
        return (
            <table className="pda-table-useraccess-form">
                <tbody>
                {membershipRows}
                </tbody>
            </table>
        )
    }

    render() {
        const { value, selectedOption } = this.state;
        const { handleSubmit, plan } = this.props;
        const isPlanLite = plan.planName === 'lite';
        let roles = [];
        let disable = true;
        let disable_memberships = true;
        if (typeof(ip_block_server_data) !== "undefined") {
            roles = ip_block_server_data.roles.map((role) => {
                return {
                    label: role,
                    value: role
                }
            });
            if(ip_block_server_data.ip_block_activated == -1) {
                disable = false;
            }
        }
        if(pda_gold_v3_data.pda_membership_integration_activated == -1) {
            disable_memberships = false;
        }
        const options = [
            { value: 'default', label: 'Use default setting' },
            { value: 'admin-user', label: 'Admin users', disabled: isPlanLite},
            { value: 'author', label: "The file's author", disabled: isPlanLite},
            { value: 'memberships', label: "Choose custom memberships", disabled: disable_memberships},
            { value: 'logger-in-user', label: 'Logged-in users', disabled: disable},
            { value: 'blank', label: 'No one', disabled: disable},
            { value: 'anyone', label: 'Anyone', disabled: disable},
            { value: 'custom-roles', label: 'Choose custom roles', disabled: disable},
        ];

        const is_memberships = ! pda_gold_v3_data.user_access_manager &&
                               ! pda_gold_v3_data.memberships_2 &&
                               ! pda_gold_v3_data.paid_memberships_pro &&
                               ! pda_gold_v3_data.woo_memberships &&
                               ! pda_gold_v3_data.woo_subscriptions;
        const form = (
            <div className="pda_form">
                <form onSubmit={handleSubmit(this.onSubmit)}>
                    <div className="wrap_select">
                        <div className="width-40">
                            <div className="ip-block">
                                <label>Grant access to this protected file for these user roles only: </label>
                            </div>
                            <Select
                                className="file_access_permision"
                                removeSelected={false}
                                name="file_access_permision"
                                value={selectedOption}
                                onChange={this.handleChange}
                                placeholder="Select..."
                                options={options}
                            />
                            {selectedOption.value == "custom-roles" &&
                            <Select
                                name="user_roles"
                                multi
                                onChange={this.handleSelectChange}
                                options={roles}
                                placeholder="Select user access"
                                value={value}
                                required={true}
                            />
                            }
                        </div>
                        {(pda_gold_v3_data.pda_membership_integration_activated == -1 && selectedOption.value == "memberships") &&
                        <div className="width-60">
                            {is_memberships &&
                            <div className="ip-block">
                                <label>Please install and activate at least one of these membership plugins:
                                    <br/>
                                    <a href="https://wordpress.org/plugins/user-access-manager/">User Access Manager</a>,
                                    <a href="https://wordpress.org/plugins/membership/"> Memberships 2</a>,
                                    <a href="https://wordpress.org/plugins/paid-memberships-pro/"> Paid Memberships Pro</a>,
                                    <a href="https://woocommerce.com/products/woocommerce-memberships/"> WooCommerce Memberships</a>,
                                    <a href="https://woocommerce.com/products/woocommerce-subscriptions/"> WooCommerce Subcriptions </a>
                                    to integrate with Prevent Direct Access Gold.
                                </label>
                            </div>
                            }
                            {this.renderMembershipTable(pda_gold_v3_data, this.state)}
                        </div>
                        }
                    </div>
                    <div className="actions">
                        <button className="btn button action" type="submit" disabled={isPlanLite}>Save changes</button>
                    </div>
                </form>
                {ip_block_server_data.ip_block_activated != -1 &&
                <p>Please install <a target="_blank"
                                     href="https://preventdirectaccess.com/extensions/wordpress-restrict-page-file-access/">Access
                    Restriction extension</a> to enable full options.</p>
                }
            </div>
        );
        return this.state.loading ? this.renderLoading() : form;
    }

    optionMembership(option) {
        if (typeof(membership_integration_data.all_members[option]) !== "undefined") {
            return membership_integration_data.all_members[option].map((user) => {
                return {
                    label: user,
                    value: user
                }
            });
        }
    }

    optionWooSubMembership(option) {
        return membership_integration_data.all_members[option].map((user) => {
            return {
                label: user['name'],
                value: user['id']
            }
        });
    }

    massageMembershipSelect(memberType, membersSelect) {
        const membershipOptions = membersSelect.members[0][memberType] !== "" ? membersSelect.members[0][memberType].split(";") : [];
        let result = [];
        if(membershipOptions.length !== 0) {
            result = membershipOptions.map((u) => {
                return {
                    value: u,
                    label: u
                }
            });
        }
        return result;
    }

    massageWooSubSelect(membersSelect) {
        const woo_subscriptions = membersSelect.members[0].woo_subscriptions !== "" ? membersSelect.members[0].woo_subscriptions.split(";") : [];
        let value_woo_subscriptions = [];
        if (woo_subscriptions.length !== 0) {
            const optionWooSubcriptions = membership_integration_data.all_members.woo_subscriptions;
            const data_woo = optionWooSubcriptions.filter(member => woo_subscriptions.map(x => Number.parseInt(x)).includes(member.id));
            value_woo_subscriptions = data_woo.map((u) => {
                return {
                    value: u['id'],
                    label: u['name']
                }
            });
        }
        return value_woo_subscriptions;
    }

    componentWillMount() {
        const {postId, getMembersSelect} = this.props;
        this.setState(...this.state, {
            loading: true
        });
        this.props.getUserRolesIpBlock(postId)
            .then(() => {
                const { RolesAccess, getMembersSelect } = this.props;
                let label = "";
                switch (RolesAccess.userRolesAccess.type) {
                    case "admin-user":
                        label = "Admin users";
                        break;
                    case "memberships":
                        label = pda_gold_v3_data.pda_membership_integration_activated == -1 ? "Choose custom memberships" : "Use default setting";
                        break;
                    case "author":
                        label = "The file's author";
                        break;
                    case "logger-in-user":
                        label = "Logged-in users";
                        break;
                    case "blank":
                        label = "No one";
                        break;
                    case "anyone":
                        label = "Anyone";
                        break;
                    case "custom-roles":
                        label = "Choose custom roles";
                        break;
                    case "default":
                        label = "Use default setting";
                        break;
                    case "":
                        label = "Use default setting";
                        break;
                }
                let roles = "";
                const value = RolesAccess.userRolesAccess.roles !== "" ? RolesAccess.userRolesAccess.roles.split(";") : "";
                if (value) {
                    roles = value.map((v) => {
                       return {
                           value: v,
                           label: v
                       }
                    });
                }
                this.setState(...this.state, {
                    value: roles,
                    selectedOption: {
                        value: RolesAccess.userRolesAccess.type !== "" ? RolesAccess.userRolesAccess.type : "default",
                        label: label
                    },
                    loading: false
                })
            });

        getMembersSelect(postId).then(() => {
            const { membersSelect } = this.props;
            this.setState(...this.state, {
                value_user_access_manager: this.massageMembershipSelect('user_access_manager', membersSelect),
                value_memberships_2: this.massageMembershipSelect('memberships_2', membersSelect),
                value_paid_memberships_pro: this.massageMembershipSelect('paid_memberships_pro', membersSelect),
                value_woo_memberships: this.massageMembershipSelect('woo_memberships', membersSelect),
                value_woo_subscriptions: this.massageWooSubSelect(membersSelect),
            });
        });
    }

    handleSelectChange = (value) => {
        this.setState(...this.state, { value });
    }

    handleChange = (selectedOption) => {
        this.setState(...this.state, { selectedOption });
    }

    handleChangeUserAccessManager = (value_user_access_manager) => {
        this.setState(...this.state, { value_user_access_manager });
    }

    handleChangeMemberships2 = (value_memberships_2) => {
        this.setState(...this.state, { value_memberships_2 });
    }

    handleChangePaidMembershipsPro = (value_paid_memberships_pro) => {
        this.setState(...this.state, { value_paid_memberships_pro });
    }

    handleChangeWooMemberships = (value_woo_memberships) => {
        this.setState(...this.state, { value_woo_memberships });
    }

    handleChangeWooSubscriptions = (value_woo_subscriptions) => {
        this.setState(...this.state, { value_woo_subscriptions });
    }

    renderLoading() {
        return (
            <div className="loading">Loading Data...</div>
        )
    }

    setValueForMemberships(valueMember) {
        let result = "";
        if(this.state[valueMember] !== "") {
            const memberships = this.state[valueMember].map((mem) => {
                return mem.value;
            })
            result = memberships.join(';');
        }
        return result;
    }

    onSubmit = (values) => {
        values.user_roles = this.setValueForMemberships('value');
        values.file_access_permision = this.state.selectedOption.value;
        if(values.file_access_permision !== "custom-roles") {
            values.user_roles = "";
        }
        values.user_access_manager = this.setValueForMemberships('value_user_access_manager');
        values.memberships_2 = this.setValueForMemberships('value_memberships_2');
        values.paid_memberships_pro = this.setValueForMemberships('value_paid_memberships_pro');
        values.woo_memberships = this.setValueForMemberships('value_woo_memberships');
        values.woo_subscriptions = this.setValueForMemberships('value_woo_subscriptions');
        const {addUserRoles, postId, handleChangeTab} = this.props;
        addUserRoles(postId, values).then(() => {
            toastr.success(MESSAGES.UPDATE_PERMISSION_OK);
            changeTab(TABS.TABLE);
            handleChangeTab(TABS.TABLE);
        });
    }
}

const Form = reduxForm({
    form: 'UserAccessForm',
})(UserAccessForm);

const mapStateToProps = state => {
    return {
        UserRoles: state.allUserRoles,
        iPBlockData: state.iPBlockData,
        RolesAccess: state.userRolesAccess,
        membersSelect: state.membersSelect,
        plan: state.getPlan,
    };
};

const mapActionsToProps = (dispatch) => {
    return bindActionCreators({getAllUserRoles, addUserRoles, getUserRolesIpBlock, getMembersSelect, getPlan}, dispatch);
}

export default connect(mapStateToProps, mapActionsToProps)(Form);



